.wrapper {
    min-height: 100vh;
    width: 100%;
    background-color: #222222;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;    
}

.start_btn {
    width: 200px;
    height: 35px;
    margin-bottom: 25px;
    cursor: pointer;
    color: white;
    outline: none;
    background-color: #222222;
    border: 1px solid white;
}

.start_btn:hover{
    background-color: white;
    color:#222222;
    transition: .3s all ease;
}

.game_info {
    margin-bottom: 25px;
    font-size: 48px;
    color: white;
}